




















.page-home {
  .head-nav-bar {
    background-color: lightskyblue;
    color: white;
    display: flex;
    height: 60px;
    align-items: center;

    .name {
      width: 100px;
      border-right: 1px solid white;
    }

    .menu {
      flex: 1;
      padding: 0 50px;
      text-align: left;
      >* {
        margin-right: 50px;
      }
    }

    .login-info {
      width: 150px;
      display: flex;
      align-items: center;

      >img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        margin-right: 10px;
      }
    }
  }
}
